module.exports = "nav.navbar {\n    background-color: #242936;\n    padding: 1rem 1rem 0.5rem 1rem;\n    white-space: nowrap;\n    text-align: center;\n    z-index: 1000;\n}\n\nbutton.menu-icon.active {\n    color: #24abf2;\n    background-color: transparent;\n}\n\n.text-theme-gray {\n    color: #9d9fa1 !important;\n}\n\nsmall.footernav-txt {\n    color: #9d9fa1 !important;\n}\n\nbutton.menu-icon {\n    color: #9d9fa1 !important;\n    margin: 0;\n    /*background-color: #F7F8F9;*/\n    display: inline-block;\n    line-height: 1.3em;\n}\n\nbutton > span > small {\n    color: #9d9fa1 !important;\n}\n\ni.fa {\n    color: #9d9fa1;\n}\n\n.mat-menu-panel {\n    min-width: 110px;\n    max-width: 120px;\n    overflow: auto;\n    -webkit-overflow-scrolling: touch;\n    max-height: calc(100vh - 48px);\n    border-radius: 2px;\n    outline: 0;\n\n}\n\n.divider {\n    border-top: 1px solid #16171A;\n    border-bottom: 1px solid #3E4250;\n}\n\n.spacer {\n    padding-bottom: 5em;\n}\n\nimg.user-image {\n    max-width: 25px;\n}\n\n/* Mobile Queries */\n\n/* Display on anything 992px and larger */\n\n@media (min-width: 992px) {\n    nav#layout-navbar {\n        display: block;\n    }\n\n    nav.navbar.navbar-footer {\n        display: none;\n    }\n}\n\n/* Small devices (landscape phones, 991px and below) */\n\n@media (max-width: 991px) {\n    nav#layout-navbar {\n        display: none;\n    }\n\n    nav.navbar.navbar-footer {\n        display: block;\n    }\n}"